import {goods,sorts} from "../../apis"
export default {
	data() {
		return {
			scrollHeight:'500px',
			leftArray:[],
			mainArray:[],
			topArr:[],
			leftIndex:0,
			scrollInto:'',
			datalist:[],
			list:[]
			
		}
	},
	onLoad(){
		uni.getSystemInfo({
			success:(res)=>{
				/* 设置当前滚动容器的高，若非窗口的高度，请自行修改 */
				this.scrollHeight=`${res.windowHeight}px`;
			}
		});
	},
	mounted(){
		this.getListData();
	},
	methods: {
		todetail(id){
			console.log(id)
			uni.navigateTo({
				url:"../goods/goods?id="+id,
			})
		},
		/* 获取列表数据 */
		getListData(){
			sorts().then(res=>{
				let [err,data]=res;
				this.datalist=data.data.data;
				let [left,main]=[[],[]];
				for(let i=0;i<this.datalist.length;i++){
					goods(this.datalist[i].id).then(res=>{
						let [err,data]=res;
						this.list=data.data.data
						left.push(this.datalist[i].name)
						main.push({
							title:this.datalist[i].name,
							list:this.list
						})
						this.$nextTick(()=>{
							this.getElementTop();
						});
					})
				}
				this.leftArray=left;
				this.mainArray=main;
			})
		},
		/* 获取元素顶部信息 */
		getElementTop(){
			/* Promise 对象数组 */
			let p_arr=[];
			
			/* 新建 Promise 方法 */
			let new_p=(selector)=>{
				return new Promise((resolve,reject)=>{
					let view = uni.createSelectorQuery().select(selector);
					view.boundingClientRect(data => {
						resolve(data.top);
					}).exec();
				})
			}
			
			/* 遍历数据，创建相应的 Promise 数组数据 */
			this.mainArray.forEach((item,index)=>{
				p_arr.push(new_p(`#item-${index}`));
			});
			console.log(p_arr)
			
			/* 所有节点信息返回后调用该方法 */
			Promise.all(p_arr).then((data)=>{
				this.topArr=data;
				console.log(this.topArr)
			});
		},
		/* 主区域滚动监听 */
		mainScroll(e){
			let top =e.detail.scrollTop;
			let index=0;
			/* 查找当前滚动距离 */
			for(let i = (this.topArr.length-1);i>=0;i--){
				/* 在部分安卓设备上，因手机逻辑分辨率与rpx单位计算不是整数，滚动距离与有误差，增加2px来完善该问题 */
				if((top+2)>=this.topArr[i]){
					index = i;
					break;
				}
			}
			this.leftIndex=(index < 0 ? 0: index);
		},
		/* 左侧导航点击 */
		leftTap(e){
			let index=e.currentTarget.dataset.index;
			console.log(index)
			this.scrollInto=`item-${index}`;
		}
	}
}
